import * as React from 'react';
import * as ReactDOMServer from 'react-dom/server';
import autobind from 'autobind-decorator';
import { PlayerPicker } from '../common';

import { BCPlayer, BrightcoveApiGateway } from '../../services/brightcove';

export class PlaylistEmbed extends React.Component<PlaylistEmbedProps, PlaylistEmbedState> {
    constructor(props: PlaylistEmbedProps, context?: any) {
        super(props, context);

        this.state = {
            sizing: 'Responsive',
            aspectRatio: '16:9',
            playerWidth: '',
            playerHeight: '',
            units: 'px'
        };
    }

    @autobind
    async handlePlayerChange(newPlayer: BCPlayer) {

        // Get embeds for the player.
        // const { bcApiGateway, accountId } = this.props;
        // const embeds = await bcApiGateway.playerManagementApi.getPlayerEmbeds(accountId, newPlayer.id);
        // console.dir(embeds);
        this.setState({
            selectedPlayer: newPlayer
        });
    }

    @autobind
    handleSizingChange(event: React.ChangeEvent<HTMLInputElement>) {
        this.setState({
            sizing: event.target.value as 'Responsive' | 'Fixed'
        });
    }

    @autobind
    handleAspectRatioChange(event: React.ChangeEvent<HTMLSelectElement>) {
        const { playerHeight } = this.state;

        this.setState({
            aspectRatio: event.target.value as '16:9' | '4:3' | 'Custom',
            playerHeight: event.target.value !== 'Custom' ? '' : playerHeight
        });
    }

    @autobind
    handleWidthChange(event: React.ChangeEvent<HTMLInputElement>) {
        this.setState({
            playerWidth: event.target.value || ''
        });
    }

    @autobind
    handleHeightChange(event: React.ChangeEvent<HTMLInputElement>) {
        this.setState({
            playerHeight: event.target.value || ''
        });
    }

    @autobind
    handleUnitsChange(event: React.ChangeEvent<HTMLSelectElement>) {
        this.setState({
            units: event.target.value as 'px' | 'em' | '%'
        });
    }

    renderEmbedCode() {
        const { accountId, playlistId } = this.props;
        const { selectedPlayer, sizing, aspectRatio, playerWidth, units } = this.state;
        let { playerHeight } = this.state;
        const playerId = selectedPlayer ? selectedPlayer.id : 'default';

        const style: React.CSSProperties = {
            width: undefined,
            height: undefined
        };

        const scriptUrl = `//players.brightcove.net/${accountId}/{${playerId}_default/index.min.js`;
        if (sizing === 'Fixed') {

            if (playerWidth) {
                switch (aspectRatio) {
                    case '16:9':
                        playerHeight = (parseInt(playerWidth, 10) * 0.5625).toString();
                        break;
                    case '4:3':
                        playerHeight = (parseInt(playerWidth, 10) * 0.75).toString();
                        break;
                    default:
                        // Do Nothing.
                        break;
                }
            }

            if (units !== 'px') {
                if (playerWidth) {
                    style.width = playerWidth + units;
                }

                if (playerHeight) {
                    style.height = playerHeight + units;
                }
            }

            return (
                <>
                    <video
                        data-playlist-id={playlistId}
                        data-account={accountId}
                        data-player={playerId}
                        data-embed="default"
                        data-application-id=""
                        className="video-js"
                        controls={true}
                        width={units === 'px' ? playerWidth : ''}
                        height={units === 'px' ? playerHeight : ''}
                        style={style}
                    />
                    <script src={scriptUrl} />
                </>
            );
        }

        let maxWidth = '100%';
        if (playerWidth) {
            maxWidth = playerWidth + units;
        }

        let topPadding = '0%';
        switch (aspectRatio) {
            case '16:9':
                if (playerWidth) {
                    playerHeight = (parseInt(playerWidth, 10) * 0.5625).toString();
                }
                break;
            case '4:3':
                if (playerWidth) {
                    playerHeight = (parseInt(playerWidth, 10) * 0.75).toString();
                }
                break;
            default:
                // Do Nothing
                break;
        }

        if (playerWidth && playerHeight) {
            topPadding = (parseInt(playerHeight, 10) / parseInt(playerWidth, 10) * 100).toPrecision(4) + '%';
        }

        return (
            <div style={{ position: 'relative', display: 'block', maxWidth: maxWidth }}>
                <div style={{ paddingTop: topPadding }}>
                    <video
                        data-playlist-id={playlistId}
                        data-account={accountId}
                        data-player={playerId}
                        data-embed="default"
                        data-application-id=""
                        className="video-js"
                        controls={true}
                        style={{ position: 'absolute', top: '0px', right: '0px', bottom: '0px', left: '0px', width: '100%', height: '100%' }}
                    />
                    <script src={scriptUrl} />
                </div>
            </div >
        );
    }

    render() {
        const { bcApiGateway, accountId, playlistId } = this.props;
        const { selectedPlayer, sizing, aspectRatio, playerWidth, playerHeight, units } = this.state;

        const playerPreviewUrl = `https://players.brightcove.net/${accountId}/${selectedPlayer ? selectedPlayer.id : 'default'}_default/index.html?playlistId=${playlistId}`;
        const embedCode = ReactDOMServer.renderToStaticMarkup(this.renderEmbedCode());

        return (
            <>
                <div className="form-horizontal">
                    <div className="form-group">
                        <label>Select Player</label>
                        <PlayerPicker bcApiGateway={bcApiGateway} accountId={accountId} selectedPlayerId={selectedPlayer && selectedPlayer.id} onChange={this.handlePlayerChange} />
                    </div>
                </div>
                {selectedPlayer &&
                    <div className="form-horizontal">
                        <div className="form-group">
                            {selectedPlayer.id === 'default' &&
                                <label>This is an instance of the Brightcove Default Player. You can modify this or choose to create your own.</label>
                            }
                            <div>
                                <iframe
                                    style={{ width: '100%', height: '400px' }}
                                    src={`//players.brightcove.net/${accountId}/${selectedPlayer.id}_default/index.html?playlistId=${playlistId}`}
                                />
                            </div>
                        </div>
                        <div className="form-group">
                            <label>Preview in browser</label>
                            <div>
                                <a href={playerPreviewUrl} target="_blank">{playerPreviewUrl}</a>
                            </div>
                        </div>
                    </div>
                }
                {selectedPlayer &&
                    <>
                        <div className="row">
                            <div className="col-md-6">
                                <h4>Copy Code</h4>
                                <div>Copy the appropriate code below and paste it into your web page or app.</div>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-md-6" />
                            <div className="col-md-6">
                                Change Player Size<br /><br />
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-md-6">
                                <textarea style={{ width: '100%', height: '300px' }} value={embedCode} onChange={() => null} />
                            </div>
                            <div className="col-md-6">
                                <label>Sizing</label>
                                <div className="input-group">
                                    <label className="radio-inline searchScope">
                                        <input
                                            type="radio"
                                            name="sizing"
                                            value="Responsive"
                                            checked={sizing === 'Responsive'}
                                            onChange={this.handleSizingChange}
                                        /> Responsive
                                    </label>
                                    <label className="radio-inline searchScope">
                                        <input
                                            type="radio"
                                            name="sizing"
                                            value="Fixed"
                                            checked={sizing === 'Fixed'}
                                            onChange={this.handleSizingChange}
                                        /> Fixed
                                    </label>
                                    <div className="input-group">
                                        <label>Aspect Ratio</label>
                                        <select className="form-control" value={aspectRatio} onChange={this.handleAspectRatioChange}>
                                            <option>16:9</option>
                                            <option>4:3</option>
                                            <option>Custom</option>
                                        </select>
                                    </div>
                                    <div className="row">
                                        <div className="col-md-6">
                                            <div className="input-group">
                                                <label>Player Width</label>
                                                <input className="form-control" type="number" placeholder="auto" value={playerWidth} onChange={this.handleWidthChange} />
                                            </div>
                                        </div>
                                        <div className="col-md-6">
                                            <div className="input-group">
                                                <label>Player Height</label>
                                                <input
                                                    className="form-control"
                                                    type="number"
                                                    placeholder="auto"
                                                    value={playerHeight}
                                                    disabled={aspectRatio !== 'Custom'}
                                                    onChange={this.handleHeightChange}
                                                />
                                            </div>
                                        </div>
                                    </div>
                                    <div className="input-group">
                                        <label>Units</label>
                                        <select className="form-control" value={units} onChange={this.handleUnitsChange}>
                                            <option>px</option>
                                            <option>em</option>
                                            <option>%</option>
                                        </select>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </>
                }
            </>
        );
    }
}

export interface PlaylistEmbedProps {
    bcApiGateway: BrightcoveApiGateway;
    accountId: string;
    playlistId: string;
}

export interface PlaylistEmbedState {
    selectedPlayer?: BCPlayer;
    sizing: 'Responsive' | 'Fixed';
    aspectRatio: '16:9' | '4:3' | 'Custom';
    playerWidth?: string;
    playerHeight?: string;
    units: 'px' | 'em' | '%';
}


