import * as React from 'react';
import { Button, Modal } from 'react-bootstrap';
import { BootstrapTable, TableHeaderColumn } from 'react-bootstrap-table';
import { VideoEmbed } from '../embeds';

import { AccountPicker, Spinner } from '../common';
import autobind from 'autobind-decorator';
import { filter, orderBy } from 'lodash';

import { BrightcoveApiGateway, BCGetVideosParams } from '../../services/brightcove';
import { SPListGateway, Account, Video } from '../../services/sharepoint';
import { ConnectorContext } from '../../Common';
import { MVideo, mapSPVideoToMVideo, mapBCVideoToMVideo } from './mVideo';

import './ManageVideos.css';

enum SearchTarget {
    SharePoint = 'SharePoint',
    VideoCloud = 'VideoCloud',
}

enum SortField {
    Name = 'name',
    Relevance = 'relevance',
    CreationDate = 'creationDate',
    StartDate = 'startDate'
}

enum SortOrder {
    Ascending = 'ascending',
    Descending = 'descending'
}

export class ManageVideos extends React.Component<ManageVideosProps, ManageVideosState> {
    constructor(props: ManageVideosProps, context?: any) {
        super(props, context);

        this.state = {
            searchTerms: '',
            searchTarget: SearchTarget.SharePoint,
            sortField: SortField.Name,
            sortOrder: SortOrder.Ascending,
            isLoadingVideos: true,
            showEmbedModal: false
        };
    }

    @autobind
    async componentDidMount() {
        const { selectedAccount } = this.state;

        if (selectedAccount) {
            this.searchForVideosInTarget(selectedAccount);
        }
    }

    @autobind
    async handleAccountChange(selectedAccount: Account) {
        this.setState({
            selectedAccount
        });

        this.searchForVideosInTarget(selectedAccount);
    }

    @autobind
    async searchForVideosInTarget(selectedAccount: Account, searchTarget?: SearchTarget, searchParams?: SearchParameters): Promise<void> {

        if (!searchTarget) {
            searchTarget = this.state.searchTarget;
        }

        if (!searchParams) {
            searchParams = this.getSearchParameters(selectedAccount);
        }

        if (!searchParams) {
            return;
        }

        this.setState({
            isLoadingVideos: true,
        });

        let videos: Array<MVideo>;
        switch (searchTarget) {
            case SearchTarget.SharePoint:
                videos = await this.searchForSharePointVideos(selectedAccount, searchParams);
                break;
            case SearchTarget.VideoCloud:
                videos = await this.searchForVideoCloudVideos(searchParams);
                break;
            default:
                return;
        }

        this.setState({
            isLoadingVideos: false,
            videos
        });
    }

    @autobind
    async searchForSharePointVideos(selectedAccount: Account, searchParams: SearchParameters): Promise<Array<MVideo>> {
        const { spListGateway } = this.props;

        const spVideos = await spListGateway.videosList.getVideosByAccountName(selectedAccount.title);

        let result: Array<MVideo> = [];
        for (const spVideo of spVideos) {
            result.push(mapSPVideoToMVideo(spVideo));
        }

        const sortDirection = searchParams.sortOrder === SortOrder.Ascending ? 'asc' : 'desc';

        // Perform a client-side search.
        switch (searchParams.sortField) {
            case SortField.Name:
                result = orderBy(result, ['name'], [sortDirection]);
                break;
            case SortField.Relevance:
                result = orderBy(result, ['sharepointId'], [sortDirection]);
                break;
            case SortField.CreationDate:
                result = orderBy(result, ['creationDate'], [sortDirection]);
                break;
            case SortField.StartDate:
                result = orderBy(result, ['startDate'], [sortDirection]);
                break;
            default:
                // Do nothing.
                break;
        }

        this.setState({
            spVideos
        });

        return result;
    }

    @autobind
    async searchForVideoCloudVideos(searchParams: SearchParameters): Promise<Array<MVideo>> {

        const { connectorContext, bcApiGateway } = this.props;
        const playlistsApi = bcApiGateway.videosApi;

        // Map search params to 'q'
        let bcParams: BCGetVideosParams = {
            limit: 45,
            sort: searchParams.sortOrder === SortOrder.Descending ? '-' : '',
            q: searchParams.searchTerms ? `+${searchParams.searchTerms}` : ''
        };

        // Remove videos that are tagged with the addin web id.
        if (connectorContext.addinWebId) {
            bcParams.q += `-tags:"bcsp-${connectorContext.addinWebId.replace(/-/g, '')}-1"`;
        }

        switch (searchParams.sortField) {
            case SortField.Name:
                bcParams.sort += 'name';
                break;
            case SortField.Relevance:
                bcParams.sort += 'plays_total';
                break;
            case SortField.CreationDate:
                bcParams.sort += 'created_at';
                break;
            case SortField.StartDate:
                bcParams.sort += 'schedule_starts_at';
                break;
            default:
                // Do nothing.
                break;
        }

        const videos = await playlistsApi.getVideos(searchParams.accountId, bcParams);

        let result: Array<MVideo> = [];
        for (const video of videos) {
            result.push(mapBCVideoToMVideo(video));
        }
        return result;
    }

    @autobind
    getSearchParameters(selectedAccount: Account): SearchParameters {

        if (!selectedAccount) {
            throw new Error('Account must be specified.');
        }

        const { searchTerms, sortField, sortOrder } = this.state;

        return {
            accountId: selectedAccount.accountId,
            searchTerms,
            sortField,
            sortOrder,
        };
    }

    @autobind
    async handleSearchTermsChange(e: React.ChangeEvent<HTMLInputElement>) {
        this.setState({
            searchTerms: e.target.value
        });
    }

    @autobind
    handleSearchTargetChanged(newSearchTarget: SearchTarget) {
        this.setState({
            searchTarget: newSearchTarget
        });

        const { selectedAccount } = this.state;
        if (!selectedAccount) {
            return;
        }

        this.searchForVideosInTarget(selectedAccount, newSearchTarget);
    }

    @autobind
    async handleSortFieldChanged(newSortField: SortField) {
        this.setState({
            sortField: newSortField
        });

        const { selectedAccount } = this.state;
        if (!selectedAccount) {
            return;
        }

        const searchParams = this.getSearchParameters(selectedAccount);
        searchParams.sortField = newSortField;
        this.searchForVideosInTarget(selectedAccount, undefined, searchParams);
    }

    @autobind
    async handleSortOrderChanged(newSortOrder: SortOrder) {
        this.setState({
            sortOrder: newSortOrder
        });

        const { selectedAccount } = this.state;
        if (!selectedAccount) {
            return;
        }

        const searchParams = this.getSearchParameters(selectedAccount);
        searchParams.sortOrder = newSortOrder;
        this.searchForVideosInTarget(selectedAccount, undefined, searchParams);
    }

    @autobind
    showEmbedModal(videoId: string) {
        this.setState({
            showEmbedModal: true,
            embedVideoId: videoId
        });
    }

    @autobind
    hideEmbedModal() {
        this.setState({
            showEmbedModal: false
        });
    }

    @autobind
    renderVideoCell(cell: any, video: MVideo, extraData: { selectedAccount: Account }): React.ReactElement<any> {
        const { selectedAccount } = extraData;
        const { searchTarget } = this.state;

        let videoLocation = 'Brightcove';
        if (searchTarget === SearchTarget.SharePoint) {
            videoLocation = 'SharePoint';
        }

        const editVideoUrl = `#/manage-videos/edit/${selectedAccount.accountId}/${videoLocation}/${video.videoId}`;
        const tagsToDisplay = filter(video.tags, (tag) => !tag.startsWith('bcsp-'));

        return (
            <div className="result">
                <img className="resultImage col-md-2" src={video.thumbnailUrl} />
                <div className="col-md-7">
                    <div><a href={editVideoUrl}>{video.name}</a></div>
                    <div>
                        {tagsToDisplay.map((tag, i) =>
                            <Button key={i} bsSize="xs" bsStyle="success">{tag}</Button>
                        )}
                    </div>
                </div>
                <div className="col-md-3">
                    <div className="clearfix"><a className="pull-right" href={editVideoUrl}>Edit Video</a></div>
                    <div className="video-id">Video Id: {video.videoId}</div>
                    <div className="clearfix">
                        <Button
                            bsStyle="success"
                            bsSize="small"
                            className="pull-right"
                            onClick={() => this.showEmbedModal(video.videoId)}
                        >
                            Embed
                        </Button>
                    </div>
                </div>
            </div>
        );
    }

    render() {

        const {
            bcApiGateway,
            connectorContext
        } = this.props;

        const {
            selectedAccount,
            searchTarget,
            searchTerms,
            sortField,
            sortOrder,
            isLoadingVideos,
            videos,
            showEmbedModal,
            embedVideoId
        } = this.state;

        return (
            <>
                <ol className="breadcrumb">
                    <li><a href="#/">Brightcove Management</a></li>
                    <li className="active">Manage Videos</li>
                </ol>
                <div className="formContent">
                    <label>Select Account:</label>{' '}
                    <AccountPicker availableAccounts={connectorContext.availableAccounts} selectedAccount={selectedAccount} onChange={this.handleAccountChange} />
                </div>
                {selectedAccount &&
                    <>
                        <div className="form-group search row">
                            <div className="col-md-5">
                                <label>Search</label>
                                <div className="input-group">
                                    <input
                                        type="search"
                                        placeholder="Search"
                                        className="form-control"
                                        value={searchTerms}
                                        onKeyPress={(e) => e.key === 'Enter' && this.searchForVideosInTarget(selectedAccount)}
                                        onChange={this.handleSearchTermsChange}
                                    />
                                    <div className="input-group-btn searchButton">
                                        <Button onClick={() => this.searchForVideosInTarget(selectedAccount)}><i className="glyphicon glyphicon-search" /></Button>
                                    </div>
                                </div>
                            </div>
                            <div className="col-md-7">
                                <label>Target</label>
                                <div className="input-group">
                                    <label className="radio-inline searchScope">
                                        <input type="radio" checked={searchTarget === SearchTarget.SharePoint} onChange={() => this.handleSearchTargetChanged(SearchTarget.SharePoint)} />SharePoint only
                            </label>
                                    <label className="radio-inline searchScope">
                                        <input type="radio" checked={searchTarget === SearchTarget.VideoCloud} onChange={() => this.handleSearchTargetChanged(SearchTarget.VideoCloud)} />Video Cloud only
                            </label>
                                </div>
                            </div>
                        </div>
                        <hr />
                        <div>
                            <div className="col-md-3">
                                <span>Sort results by: </span>
                            </div>
                            <div className="col-md-9 searchResultsOrderByField">
                                <label className="radio-inline">
                                    <input type="radio" checked={sortField === SortField.Name} onChange={() => this.handleSortFieldChanged(SortField.Name)} />display name
                        </label>
                                <label className="radio-inline">
                                    <input type="radio" checked={sortField === SortField.Relevance} onChange={() => this.handleSortFieldChanged(SortField.Relevance)} />relevance
                        </label>
                                <label className="radio-inline">
                                    <input type="radio" checked={sortField === SortField.CreationDate} onChange={() => this.handleSortFieldChanged(SortField.CreationDate)} />creation date
                        </label>
                                <label className="radio-inline">
                                    <input type="radio" checked={sortField === SortField.StartDate} onChange={() => this.handleSortFieldChanged(SortField.StartDate)} />start date
                        </label>
                            </div>
                        </div>
                        <div>
                            <div className="col-md-3">
                                <span>Order: </span>
                            </div>
                            <div className="col-md-9 searchSortOrder">
                                <label className="radio-inline">
                                    <input type="radio" checked={sortOrder === SortOrder.Ascending} onChange={() => this.handleSortOrderChanged(SortOrder.Ascending)} />ascending
                                </label>
                                <label className="radio-inline">
                                    <input type="radio" checked={sortOrder === SortOrder.Descending} onChange={() => this.handleSortOrderChanged(SortOrder.Descending)} />descending
                                </label>
                            </div>
                        </div>
                        {isLoadingVideos &&
                            <Spinner loadingText="Loading Videos..." />
                        }
                        {!isLoadingVideos && videos &&
                            <BootstrapTable data={videos} striped={true} bordered={false} pagination={videos.length > 10} options={{ paginationPosition: 'bottom' }}>
                                <TableHeaderColumn dataField="videoId" isKey={true} dataFormat={this.renderVideoCell} formatExtraData={{ selectedAccount }}>Results</TableHeaderColumn>
                            </BootstrapTable>
                        }
                        {embedVideoId &&
                            <Modal className="embed" show={showEmbedModal} onHide={this.hideEmbedModal} bsSize="large">
                                <Modal.Header closeButton={true}>
                                    <Modal.Title>Embed Video</Modal.Title>
                                </Modal.Header>
                                <Modal.Body>
                                    <VideoEmbed
                                        bcApiGateway={bcApiGateway}
                                        accountId={selectedAccount.accountId}
                                        videoId={embedVideoId}
                                    />
                                </Modal.Body>
                                <Modal.Footer />
                            </Modal>
                        }
                    </>
                }
            </>
        );
    }
}

interface ManageVideosProps {
    connectorContext: ConnectorContext;
    bcApiGateway: BrightcoveApiGateway;
    spListGateway: SPListGateway;
}

interface ManageVideosState {
    searchTarget: SearchTarget;
    searchTerms: string;
    sortField: SortField;
    sortOrder: SortOrder;

    isLoadingVideos: boolean;
    spVideos?: Array<Video>;
    videos?: Array<MVideo>;
    selectedAccount?: Account;

    showEmbedModal: boolean;
    embedVideoId?: string;
}

interface SearchParameters {
    accountId: string;
    searchTerms: string;
    sortField: SortField;
    sortOrder: SortOrder;
}


